import { PATH } from './path.contant';

export const PAGE_TITLE = {
  [PATH.LOGIN]: 'ng nhp',
  [PATH.REGISTER]: 'ng k',
  [PATH.HOME]: 'Trang ch',

  [PATH.STORE]: 'Ca hng',
  [PATH.CROWDED]: 'ng khch',
  [PATH.FOOD]: 'Mn n',
  [PATH.PROMOTION]: 'Khuyn mi',
  [PATH.FAVORITE]: 'Mn n  thch',


  [PATH.CART]: 'Gi hng',

  [PATH.PROFILE]: 'Qun l ti khon',

  STORE_DETAIL: 'Thng tin ca hng',
  ORDERS: 'Lch s giao dch',
  BOOKMARKS: 'B su tp',
  COMMENTS: 'Lch s bnh lun',
  RATES: 'Lch s nh gi',
  USER_INFO: 'Thng tin ti khon',
  CHANGE_PASSWORD: 'i mt khu',
  USER_UPDATE: 'Cp nht ti khon',
};
const PREFIX = (title, custom = 'FreshFood') => {
  return `${custom} | ${title}`;
};
export const TITLE = (title, suffix = false, isDefault = true, prefix = true) => {
  if (isDefault) {
    title = PAGE_TITLE[title];
  }
  if (prefix) {
    if (suffix) {
      title = PREFIX(suffix, title);
    } else {
      title = PREFIX(title);
    }
  }
  return title;
};
