import { BiError } from 'react-icons/all';
import { Button } from 'antd';
import history from '../../utils/history';
import * as S from './style';

function NotFoundPage() {
  return (
    <S.NotFoundWrap>
      <div>
        <BiError />
        <h2>Li! Khng tm thy trang ny!</h2>
        <Button
          type="primary"
          onClick={() => {
            if (history.length === 0) {
              history.push('/');
            } else {
              history.goBack();
            }
          }}
        >
          Quay li
        </Button>
      </div>
    </S.NotFoundWrap>
  );
}

export default NotFoundPage;
