const toNotMark = (str) => {
  return str.toLowerCase().trim()
    .replaceAll(/(||||||||||||||||)/g, 'a')
    .replaceAll(/(||||||||||)/g, 'e')
    .replaceAll(/(||||)/g, 'i')
    .replaceAll(/(||||||||||||||||)/g, 'o')
    .replaceAll(/(||||||||||)/g, 'u')
    .replaceAll(/(||||)/g, 'y')
    .replaceAll(/()/g, 'd')
    .replaceAll('&*#39', '')
    .replaceAll(' ', '-');
};

export default toNotMark;