import { Button, Col, Form, Input, Row, Space, Spin } from 'antd';
import * as S from '../style';
import history from '../../../../utils/history';
import { useDispatch, useSelector } from 'react-redux';
import { changePasswordAction, resetErrorPasswordAction } from '../../../../redux/actions';
import { useEffect } from 'react';
import { PAGE_TITLE } from '../../../../contants';

const ChangePassword = () => {
  const userToken = localStorage.userInfo;
  const dispatch = useDispatch();
  const { responseAction: { update: { password } } } = useSelector(({ userReducer }) => userReducer);
  const [changePasswordForm] = Form.useForm();

  useEffect(() => {
    document.title = PAGE_TITLE.CHANGE_PASSWORD;
    dispatch(resetErrorPasswordAction());
  }, []);

  useEffect(() => {
    if (password.error) {
      const errors = { ...password.error };
      const fieldError = [];
      for (const field in errors) {
        fieldError.push({ name: field, errors: [errors[field][0]] });
      }
      changePasswordForm.setFields(fieldError);
    }
  }, [password]);

  return (
    <div style={{ paddingBottom: 15 }}>
      <Row gutter={20}>
        <Col span={8} />
        <Col span={16}>
          <h3 style={{
            padding: '24px 0',
            fontSize: 24,
            margin: 0,
          }}>
            i mt khu
          </h3>
        </Col>
      </Row>
      <S.UserProfile>
        <Form
          form={changePasswordForm}
          name='change-password'
          labelCol={{ span: 8 }}
          wrapperCol={{ span: 12 }}
          autoComplete='off'
          onFinish={(value) => {
            const { accessToken } = JSON.parse(userToken);
            dispatch(changePasswordAction({
              accessToken,
              data: value,
            }));
          }}
        >
          <Form.Item
            label='Mt khu c:'
            name='oldPassword'
            rules={[
              { required: true, message: 'Vui lng nhp mt khu c!' },
              { min: 6, message: 'Mt khu ti thiu 6 k t!' },
            ]}
          >
            <Input.Password placeholder='Nhp mt khu c' />
          </Form.Item>
          <Form.Item
            label='Mt khu mi:'
            name='password'
            rules={[
              { required: true, message: 'Vui lng nhp mt khu mi!' },
              { min: 6, message: 'Mt khu ti thiu 6 k t!' },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || value.length < 6 || getFieldValue('oldPassword') !== value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error('Mt khu mi phi khc mt khu c!'));
                },
              }),
            ]}
          >
            <Input.Password placeholder='Nhp mt khu mi' />
          </Form.Item>
          <Form.Item
            label='Nhp li mt khu:'
            name='confirmPassword'
            rules={[
              { required: true, message: 'Vui lng xc nhn mt khu!' },
              { min: 6, message: 'Mt khu ti thiu 6 k t!' },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || value.length < 6 || getFieldValue('password') === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error('Nhp li mt khu khng khp!'));
                },
              }),
            ]}
          >
            <Input.Password placeholder='Nhp li mt khu' />
          </Form.Item>
          <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
            <Space>
              <Button
                disabled={password.load}
                style={{
                  background: '#3380d8',
                  color: 'white',
                }}
                htmlType='submit'
              >
                i mt khu
              </Button>
              <Button onClick={() => history.push('/profile/user-info')}>Hy</Button>
              {password.load && <Spin />}
            </Space>
          </Form.Item>
        </Form>
      </S.UserProfile>;
    </div>
  );
};
export default ChangePassword;
