import { Col, Row } from 'antd';
import { useEffect, useRef, useState } from 'react';
import {
  BiStore,
  FiPercent,
  GiCheckMark,
  GiSpellBook,
  IoFastFoodOutline,
} from 'react-icons/all';
import camelCaseKeys from 'camelcase-keys';

import { PATH, SERVER_CLIENT_API_URL } from '../../../../../contants';
import * as S from './style';

const SectionListed = () => {
  const [{ totalFoods, totalPromotions, totalStores, totalUsers }, setTotalList] = useState({
    totalUsers: '--',
    totalStores: '--',
    totalFoods: '--',
    totalPromotions: '--',
  });

  const section = useRef(0);
  const getDataTotal = async () => {
    await fetch(`${SERVER_CLIENT_API_URL}/listed`)
      .then((response) => response.text())
      .then((result) => {
        const totalData = camelCaseKeys(JSON.parse(result));
        setTotalList({
          ...totalData,
        });
      })
      .catch((error) => console.log('error', error));
  };

  useEffect(() => {
    getDataTotal();
  }, []);

  return (
    <S.Introduce ref={section}>
      <div>
        <Row justify='space-around'>
          <Col lg={10} md={12} xs={24} className='listed-header'>
            <h2>
              <span>FreshFood</span> - Thc phm online
            </h2>
            <p className='note-store'>
              Trong din bin dch bnh SARS-CoV-2 ang ngy cng phc tp,
              FreshFood ang phn u tng ngy  chung sc nh tan dch bnh, h
              tr v phc v khch hng cng vt qua thi im kh khn.
            </p>
          </Col>
          <Col lg={10} md={12} xs={24} className='listed-header'>
            <ul>
              <li>
                <GiCheckMark /> Giao hng tn ni
              </li>
              <li>
                <GiCheckMark /> Cam kt cht lng - uy tn
              </li>
              <li>
                <GiCheckMark /> Bnh n gi
              </li>
              <li>
                <GiCheckMark /> Vui lng khch t - va lng khch mua
              </li>
            </ul>
          </Col>
        </Row>
        <div className='list'>
          <Row justify='center'>
            <Col span={6}>
              <S.IntroduceLink to={PATH.STORE} color={'#ff85c0'}>
                <figure>
                  <BiStore />
                  <figcaption>
                    <p>{totalStores}</p>
                    <p className='list-name'>Ca hng</p>
                  </figcaption>
                </figure>
                <span />
              </S.IntroduceLink>
            </Col>
            <Col span={6}>
              <S.IntroduceLink to={PATH.FOOD} color={'#ffd666'}>
                <figure>
                  <IoFastFoodOutline />
                  <figcaption>
                    <p>{totalFoods}</p>
                    <p className='list-name'>Mn n</p>
                  </figcaption>
                </figure>
                <span />
              </S.IntroduceLink>
            </Col>
            <Col span={6}>
              <S.IntroduceLink to={PATH.PROMOTION} color={'#85a5ff'}>
                <figure>
                  <FiPercent />
                  <figcaption>
                    <p>{totalPromotions}</p>
                    <p className='list-name'>Khuyn mi</p>
                  </figcaption>
                </figure>
                <span />
              </S.IntroduceLink>
            </Col>
            <Col span={6}>
              <S.IntroduceLink to={PATH.CROWDED} color={'#5cdbd3'}>
                <figure>
                  <GiSpellBook />
                  <figcaption>
                    <p>{totalUsers}</p>
                    <p className='list-name'>Ngi tiu dng</p>
                  </figcaption>
                </figure>
                <span />
              </S.IntroduceLink>
            </Col>
          </Row>
        </div>
      </div>
    </S.Introduce>
  );
};
export default SectionListed;
